/* Experiences.css */
.experiences ul {
  list-style: none;
  padding: 0;
}
.experience-item h3 {
  margin: 0;
}
.details {
  margin-top: 0.5rem;
  padding-left: 1rem;
}
.experiences ul {
  list-style: none;
  padding-left: 0;
}

.experiences > ul > li {
  margin-bottom: 2rem;
  border-left: 3px solid #007acc;
  padding-left: 1rem;
}

.experiences h3 {
  margin: 0 0 0.3rem;
  color: #007acc;
  font-weight: 700;
  font-size: 1.2rem;
}

.experiences time {
  font-style: italic;
  color: #555;
  font-size: 0.9rem;
}

.experiences .details {
  margin-top: 0.5rem;
  padding-left: 1.2rem;
  list-style: disc;
  color: #333;
  font-size: 0.95rem;
}

.experience-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.experience-card {
  width: 280px;
  height: 200px;
  perspective: 1000px; /* perspective pour le 3D */
  cursor: pointer;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  background-color: #fff;
}

.experience-card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 12px;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card-front {
  background: #f9f9f9;
  color: #333;
}

.card-logo {
  width: 60px;
  height: 60px;
  object-fit: contain;
  margin-bottom: 1rem;
  border-radius: 8px;
}

.card-back {
  background: #ffffff;
  color: white;
  transform: rotateY(180deg);
  overflow-y: auto;
}

.card-back .details {
  padding-left: 1rem;
  text-align: left;
  margin: 0;
}

.card-back .details li {
  margin-bottom: 0.5rem;
}
